html {
  font-size: 10px; }
  @media screen and (max-width: 780px) {
    html {
      font-size: 9px; } }

body {
  font-family: "Fira Sans", sans-serif;
  font-size: 1.4rem;
  color: #222; }

*, *:before, *:after {
  box-sizing: border-box; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

ul, li {
  list-style-type: none;
  margin: 0;
  padding: 0; }

img {
  max-width: 100%;
  border-style: none;
  height: auto; }

textarea:focus,
input:focus {
  outline: none; }

a {
  transition: all .3s; }
  a:hover {
    opacity: .7; }

p {
  color: #222;
  font-size: 18px; }

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #398860;
  margin: 0 auto;
  padding: 0; }

.site {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
  .site__content {
    flex-grow: 1; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px; }

.col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% - 30px); }

.col-10-center {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  margin-left: calc(8.33333% + 15px);
  margin-right: calc(8.33333% + 15px);
  width: calc(83.33333% - 30px); }
  @media screen and (max-width: 1100px) {
    .col-10-center {
      margin-left: calc(0% + 15px); } }
  @media screen and (max-width: 1100px) {
    .col-10-center {
      margin-right: calc(0% + 15px); } }
  @media screen and (max-width: 1100px) {
    .col-10-center {
      width: calc(100% - 30px); } }

.col-8-center {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  margin-left: calc(16.66667% + 15px);
  margin-right: calc(16.66667% + 15px);
  width: calc(66.66667% - 30px); }
  @media screen and (max-width: 960px) {
    .col-8-center {
      margin-left: calc(0% + 15px); } }
  @media screen and (max-width: 960px) {
    .col-8-center {
      margin-right: calc(0% + 15px); } }
  @media screen and (max-width: 960px) {
    .col-8-center {
      width: calc(100% - 30px); } }

.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.text-left {
  text-align: left; }

[class*="ico-"] {
  display: inline-block;
  vertical-align: middle; }

.ico-location {
  background-image: url(../img/sprite/sprite.png);
  background-position: 0px 0px;
  width: 30px;
  height: 30px; }

.logo__img {
  width: 165px;
  min-width: 130px; }

.back-to-top {
  position: fixed;
  bottom: 40px;
  right: 40px;
  display: none;
  cursor: pointer;
  animation-duration: .5s;
  animation-fill-mode: both;
  animation-name: fadeInUp; }
  .back-to-top__inner {
    background: #398860;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    transition: all .3s;
    text-align: center; }
  .back-to-top.is-active {
    display: block; }
  .back-to-top:hover .back-to-top__inner {
    opacity: .7; }

.submenu {
  display: none;
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 0;
  min-width: 300px;
  background: #398860;
  padding: 10px; }
  .submenu .menu__item {
    padding: 5px;
    display: block; }

.menu {
  display: flex;
  justify-content: space-between;
  justify-content: space-around;
  letter-spacing: 0.34px;
  color: #fff;
  font-size: 17px;
  font-weight: 700; }
  @media screen and (max-width: 1100px) {
    .menu {
      flex-direction: column;
      text-align: left;
      background: #1a8ea1;
      max-width: 320px;
      margin-left: auto;
      padding: 10px 20px; } }
  .menu__item {
    margin-right: 15px;
    position: relative; }
    .menu__item:last-child {
      margin-right: 0; }
    .menu__item:hover .submenu {
      display: block; }
  .menu__child-icon {
    width: 14px;
    height: 14px;
    fill: #398860;
    position: relative;
    top: 2px; }
  .menu__link {
    color: #fff;
    text-decoration: none;
    text-transform: uppercase; }
    @media screen and (max-width: 1100px) {
      .menu__link {
        padding: 5px 0;
        display: block; } }
    .menu__link span {
      text-decoration: underline; }

.mob-menu__btn {
  display: none;
  width: 32px;
  height: 32px;
  cursor: pointer;
  transition: all .3s;
  position: relative; }
  .mob-menu__btn:hover span {
    background: #398860; }
  .mob-menu__btn span {
    display: block;
    height: 2px;
    width: 32px;
    background: #fff;
    position: absolute;
    z-index: 1;
    transition: all .2s; }
    .mob-menu__btn span:nth-child(1) {
      top: 6px; }
    .mob-menu__btn span:nth-child(2) {
      top: 50%;
      margin-top: -1px; }
    .mob-menu__btn span:nth-child(3) {
      bottom: 6px; }
  .mob-menu__btn.is-open span:nth-child(1) {
    transform: rotate(45deg);
    top: 50%;
    margin-top: -1px; }
  .mob-menu__btn.is-open span:nth-child(2) {
    display: none; }
  .mob-menu__btn.is-open span:nth-child(3) {
    transform: rotate(-45deg);
    top: 50%;
    margin-top: -1px; }
  .mob-menu__btn.is-open .menu,
  .mob-menu__btn.is-open + div .menu {
    display: block; }
  @media screen and (max-width: 1100px) {
    .mob-menu__btn {
      display: block;
      position: relative;
      z-index: 10; } }

.btn,
a.btn {
  display: block;
  cursor: pointer;
  transition: all .3s;
  text-decoration: none;
  color: #000;
  background-color: #fff;
  border: 1px solid #000;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 0.2px;
  text-align: center; }
  .btn:hover,
  a.btn:hover {
    opacity: .7; }

.tabs__links {
  padding-bottom: 20px; }

.tab__link {
  cursor: pointer;
  border: 1px solid #000;
  display: inline-block;
  padding: 5px 10px;
  margin-right: 20px;
  transition: 0.3s; }
  .tab__link:last-child {
    margin-right: 0; }
  .tab__link:hover {
    opacity: .7; }
  .tab__link.is-active {
    border-color: #0f0; }

.tab__content {
  display: none;
  border: 1px solid #c0c;
  padding: 20px 10px;
  animation: fadeIn 1s; }
  .tab__content.is-active {
    display: block; }

.accordion__title {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  width: 100%;
  text-align: left;
  transition: 0.4s;
  padding: 5px 10px;
  border: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .accordion__title:after {
    content: '\002B';
    color: #777;
    margin-left: 5px; }
  .accordion__title:hover {
    background-color: #ccc; }
  .accordion__title.is-active {
    background-color: #ccc; }
    .accordion__title.is-active:after {
      content: "\2212"; }

.accordion__panel {
  padding: 0 18px;
  background-color: #fff;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out; }

.site-title {
  font-size: 4.3rem;
  font-weight: 400;
  text-transform: uppercase; }
  @media screen and (max-width: 560px) {
    .site-title {
      font-size: 3rem; } }

.section-title {
  color: #a9a9a9;
  font-size: 2.4rem;
  font-weight: bold;
  letter-spacing: 0.04rem;
  margin-bottom: 3rem; }
  .section-title--black {
    color: #000; }

.table-responsive {
  width: 100%;
  overflow-x: auto;
  padding-bottom: 10px; }

.table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.32px; }
  .table thead tr {
    background: #b98a38; }
  .table thead th {
    color: #060400;
    font-weight: 400;
    padding: 5px 25px;
    text-align: center; }
    .table thead th:nth-child(1), .table thead th:nth-child(2) {
      text-align: left; }
  .table tbody tr {
    background: #d0d2d3; }
    .table tbody tr:nth-child(2n) {
      background: #e9e9ea; }
  .table tbody td {
    color: #000000;
    padding: 5px 25px;
    text-align: center; }
    .table tbody td:nth-child(1), .table tbody td:nth-child(2) {
      text-align: left; }

/******************** СТРАНИЦЫ ********************/
